<template>
  <section class='comments'>
    <h1>Отзывы наших покупателей</h1>
      <q v-for='(comment, index) in selectedComments' v-bind:key='index'>
        <span>{{ comment.author }}:</span>
        {{ comment.text }}
      </q>
      <social-component></social-component>
  </section>
</template>

<script>
import SocialComponent from './social.vue';

const COMMENTS_COUNT = 3;

export default {
  name: 'Comments',
  components: { SocialComponent },
  data () {
    return {
      comments: [
        {
          'author': 'Елизавета',
          'text': `
            Торт очень вкусный, всем понравился,
            спасибо огромное вам, буду еще обращаться 😍.
          `
        },
        {
          'author': 'Кристина',
          'text': `
            Спасибо большое за 🎂!!!!!! Он был потрясающий, брат в восторге!!!!!!
            Я не ожидала что он будет настолько вкусным !!!!!!!!!
            Вы мастер своего дела !!!!!
          `
        },
        {
          'author': 'Мария',
          'text': `
            Здравствуйте , ещё раз хочу вас поблагодарить за тортик.
            Парню всё понравилось, получилось великолепно и очень вкусно.
            Спасибо вам большое 💋❤
          `
        },
        {
          'author': 'Мария',
          'text': `
            Здравствуйте , ещё раз хочу вас поблагодарить за тортик.
            Парню всё понравилось, получилось великолепно и очень вкусно.
            Спасибо вам большое 💋❤
          `
        },
        {
          'author': 'Дина',
          'text': `
            Спасибо,было очень вкусно всем понравилось)))
          `
        },
        {
          'author': 'Инна',
          'text': `
            Огромное спасибо за тортик! Все были род большим впечатлением,
            ведь это был сюрприз для всех. Ребёнок мой сказал-круто!
          `
        },
        {
          'author': 'Юлия',
          'text': `
            Спасибо вам большое, такого вкусного тортика я ещё не кушала 😍
            Подружка в восторге.
          `
        },
        {
          'author': 'Алина',
          'text': `
            Спасибо вам огромное!!))) великолепный торт!!
            Все просто в восторге!!!!! Вы великолепный мастер!)))
          `
        }
      ]
    };
  },
  created () {
    this.shuffleComments();
  },
  computed: {
    selectedComments () {
      return this.comments.slice(0, COMMENTS_COUNT);
    }
  },
  methods: {
    shuffleComments () {
      for (let i = this.comments.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [this.comments[i], this.comments[j]] =
          [this.comments[j], this.comments[i]];
      }
    }
  }
};
</script>

<style scoped lang='scss'>
.comments {
  color: white;
  width: 100%;
  padding: 20px 0;
  background: white;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    flex-direction: row;
    q {
      box-shadow: 2px 2px 5px $shadow;
      background: $commentsBackground;
      padding: 20px;
      border-radius: 10px;
      margin: 10px;
      max-width: 300px;
      text-align: justify;
    }
    h1 {
      width: 100%;
      color: $fontColorDark;
      text-align: center;
      margin: 40px 0;
    }
}
</style>



