import HomePage from '@/pages/home.vue';
import GalleryPage from '@/pages/gallery.vue';

export default [
  {
    path: '/',
    name: 'Home',
    component: HomePage
  },
  {
    path: '/gallery/:album_id/:photo_index',
    name: 'Photo',
    component: GalleryPage
  },
  {
    path: '/album/:album_id',
    name: 'HomeAlbum',
    component: HomePage
  },
  {
    path: '*',
    redirect: {
      name: 'Home'
    },
    name: 'NotFound'
  }
];



