<template>
  <section class='price'>
    <h1>Условия заказа. Расчёт стоимости одного торта</h1>
    <div id='price-calculator'>
      <div id='price-form'>
      <span>Вес, кг:</span>
      <span class='value'>
        <input type='number' min='2.5' max='10' step="0.5" v-model.number='weight'
               v-on:keypress='checkNumberInput'>
      </span>
      <span class='description'>
        <span v-if='weight >= 2.5 && weight <= 10'>
          Этого количества хватит {{ guestsCount }}
          <span v-if='guestsCount % 10 == 1'>гостю</span>
          <span v-else>гостям</span>
        </span>
        <span v-if='weight < 2.5'>
          Минимальный заказ 2.5 кг
        </span>
        <span v-if='weight > 10'>
          Максимальный заказ 10 кг
        </span>
      </span>

      <span>Торт:</span>
      <span class='value'>
        <div class='select'>
          <p class='box'>{{ cake.name }}</p>
          <div class='popup'>
            <p v-for='cake in cakes' v-bind:key='cake.name'
               v-on:click='setCake(cake)' class='option'>
            {{ cake.name }}
            </p>
          </div>
        </div>
      </span>
      <span class='description'>
        {{ cake.description }}
      </span>

      <span>Тара:</span>
      <span class='value'>
        <label class='radio'>
          <i class='material-icons' v-if='!addPackage'>radio_button_unchecked</i>
          <i class='material-icons' v-else>radio_button_checked</i>
          Да
          <input name='package' v-model='addPackage' v-bind:value='true' type='radio' id='package_yes'>
        </label>
        <label class='radio'>
          <i class='material-icons' v-if='addPackage'>radio_button_unchecked</i>
          <i class='material-icons' v-else>radio_button_checked</i>
          Нет
          <input name='package' v-model='addPackage' v-bind:value='false' type='radio' id='package_no'>
        </label>
      </span>
      <span class='description'>
        Упаковка для торта.
      </span>

      <span v-if='isValid'>Итог: </span>
      <span v-if='isValid' class='value'>
        {{ price }} + Скидка {{ discount }}%
      </span>
      <span v-if='isValid' id='price'>={{ priceWithDiscount }} руб</span>
      </div>
      <p id='note'>В сумме не учитываются декоративные элементы (от 100 до 1000 руб).</p>
    </div>
  </section>
</template>

<script>
const DEFAULT_DISCOUNT = 10;
const PACKAGE_PRICE = 100;
const DEFAULT_WEIGHT = 2.5;

export default {
  name: 'MainInfo',
  data () {
    return {
      discount: DEFAULT_DISCOUNT,
      addPackage: false,
      cake: undefined,
      cakes: [
        {
          name: 'Лимонно-ягодный бисквит',
          price: 700,
          description: 'Лимонный бисквит, крем с маскарпоне, ягодное конфи.'
        },
        {
          name: 'Шоколадный шифон',
          price: 700,
          description: 'Шоколадный шифоновый бисквит, творожный крем или крем-чиз.'
        },
        {
          name: 'Три шоколада',
          price: 900,
          description: 'Шоколадно-шифоновый бисквит и три мусса (черный, молочный, белый шоколад).'
        },
        {
          name: 'Красный бархат',
          price: 800,
          description: 'Сливочный бисквит, творожный крем или крем-чиз.'
        },
        {
          name: 'Молочная девочка',
          price: 700,
          description: 'Нежные коржи на основе сгущенного молока, легкий сливочный крем, фруктово-ягодное ассорти.'
        },
        {
          name: 'Сникерс',
          price: 1000,
          description: 'Шоколадно-шифоновый бисквит, карамель, сливочный крем, шоколадный мусс.'
        },
        {
          name: 'Йогуртовый',
          price: 800,
          description: 'Апельсиновый бисквит, йогуртовое суфле.'
        },
        {
          name: 'Медовик',
          price: 600,
          description: 'Медовые коржи, сметанно-сливочный крем.'
        }
      ],
      weight: DEFAULT_WEIGHT
    };
  },
  created () {
    let cakeIndex = Math.floor(Math.random() * this.cakes.length);
    this.cake = this.cakes[cakeIndex];
  },
  methods: {
    setCake (cake) {
      this.cake = cake;
    },
    checkNumberInput (event) {
      let allowed = (event.key === 'Backspace' ||
                     event.key === 'Delete' ||
                     event.key === 'ArrowLeft' ||
                     event.key === 'ArrowRight' ||
                     event.ctrlKey ||
                     event.key === '.' ||
                     !isNaN(parseInt(event.key)));
      if (!allowed) {
        event.preventDefault();
      }
    }
  },
  computed: {
    isValid () {
      return (this.weight >= 2.5 && this.weight <= 10);
    },
    price () {
      return Math.floor(this.priceWithDiscount * (1 + this.discount / 100.0));
    },
    priceWithDiscount () {
      return Math.floor(this.cake.price * this.weight +
                       (this.addPackage ? PACKAGE_PRICE : 0));
    },
    guestsCount () {
      return Math.ceil(this.weight / 0.2);
    }
  }
};
</script>

<style scoped lang='scss'>
#note {
  margin-top: 20px;
  max-width: 420px;
  text-align: center;
}

.price {
  color: $fontColorLight;
  width: 100%;
  max-width: 100%;
  height: 700px;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url('../assets/second.jpg');
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  h1 {
    text-shadow: 0 5pt 4pt $shadowDark;
    margin-bottom: 40px;
    @include medium_screen {
      font-size: 24pt;
    }
  }

  #price-form {
    text-align: center;
    display: grid;
    align-items: center;
    @include large_screen {
      font-size: 16pt;
      grid-template-columns: auto auto 150px;
      > {
       padding: 4px 0
      }
      .description {
        font-size: 12pt;
      }
    }
    @include medium_screen {
      font-size: 12pt;
      grid-template-columns: auto;
      > {
        padding: 20px 0
      }
      .description {
        font-size: 10pt;
      }
    }
  }

  #price-calculator {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: $opacityBackgroundLight;
    color: $fontColorDark;
    padding: 25px;
    box-sizing: border-box;
    max-width: 100%;
    border-radius: 10px;
    h4 {
      text-align: center
    }
    .description {
      margin-bottom: 10px;
      color: rgb(75, 75, 75);
    }
    .value {
      margin: 0 30px;
      display: flex;
      justify-content: center;
    }
    #price {
      font-weight: bold;
    }
  }
}
</style>



