<template>
  <div>
    <header-component></header-component>
    <why-we-component class="block"></why-we-component>
    <comments-component class="block"></comments-component>
    <price-component class="block"></price-component>
    <photos-component id="photos" ref="photos" v-on:photoClicked="photoClicked" class="block"></photos-component>
  </div>
</template>

<script>
import HeaderComponent from '@/components/header.vue';
import WhyWeComponent from '@/components/why_we.vue';
import CommentsComponent from '@/components/comments.vue';
import PriceComponent from '@/components/price.vue';
import PhotosComponent from '@/components/photos.vue';

export default {
  name: 'home',
  components: {
    HeaderComponent,
    WhyWeComponent,
    CommentsComponent,
    PriceComponent,
    PhotosComponent
  },
  data () {
    return { };
  },
  mounted () {
    if (this.$route.params.album_id !== undefined) {
      window.ALBUMS.forEach(album => {
        if (album.id === parseInt(this.$route.params.album_id)) {
          this.$refs.photos.setAlbum(album);
          this.$scrollTo(document.getElementById('photos'));
          return false;
        }
      });
    }
  },
  methods: {
    photoClicked (album, index) {
      window.preload = new Image();
      window.preload.src = album.photos[index].large;
      this.$router.push({
        name: 'Photo',
        params: {
          album_id: album.id,
          photo_index: index
        }
      });
    }
  }
};
</script>

<style scoped lang="scss">
// @import "../styles/_colors.scss";
.block {
  box-shadow: 0 -2px 10px $shadowDark;
}
</style>



