<template>
  <div>
    <div id='gallery'>
      <i v-on:click='home' class='material-icons button'>home</i>
      <div ref='photo' id='photo'
           v-bind:style='photoStyle' v-bind:class='photoClass'>
        <div class='arrow' v-on:click='previous'>
          <i class='material-icons'>keyboard_arrow_left</i>
        </div>
        <div class='arrow' v-on:click='next' id='arrow-right'>
          <i class='material-icons'>keyboard_arrow_right</i>
        </div>
      </div>
      <div id='sidebar'>
        <logo-component></logo-component>
        <div id='thumbnails'>
          <img v-bind:src='photo.medium' v-for='(photo, index) in photos'
              v-bind:key='photo.medium' v-bind:class='thumbnailClass(index)'
              v-on:click='setPhoto(index)' v-bind:id='getThumbnailId(index)'>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import LogoComponent from '../components/logo.vue';

const PRELOAD_COUNT = 2;

export default {
  name: 'PhotoViewer',
  components: { LogoComponent },
  data () {
    return {
      index: 0,
      photos: ['/static/all/1.jpg', '/static/all/2.jpg', '/static/all/3.jpg'],
      animationState: 0,
      direction: 0,
      albumId: 0
    };
  },
  beforeRouteUpdate (to, from, next) {
    if (parseInt(to.params.photo_index) !== this.index) {
      this.home();
    } else {
      next();
    }
  },
  created () {
    window.addEventListener('keydown', (event) => {
      if (event.key === 'ArrowLeft' || event.key === 'ArrowDown') {
        this.previous();
      } else if (event.key === 'ArrowRight' || event.key === 'ArrowUp') {
        this.next();
      }
    });
    let BreakException = {};
    let albumFound = false;
    try {
      window.ALBUMS.forEach(album => {
        if (album.id === parseInt(this.$route.params.album_id)) {
          let photoIndex = parseInt(this.$route.params.photo_index);
          if (photoIndex >= 0 && photoIndex < album.photos.length) {
            this.set(album, parseInt(photoIndex));
            albumFound = true;
          }
          throw BreakException;
        }
      });
    } catch (e) {
      if (e !== BreakException) throw e;
    }
    if (!albumFound) this.$router.push({ name: 'NotFound' });
  },
  mounted () {
    this.$refs.photo.addEventListener('wheel', (event) => {
      if (event.deltaY >= 2) this.next();
      else if (event.deltaY <= -2) this.previous();
    }, false);
  },
  methods: {
    getThumbnailId (index) {
      return `thumbnail_${index}`;
    },
    set (album, index) {
      this.photos = album.photos;
      this.albumId = album.id;
      this.index = index;
      this.preload(index);
      this.$router.push({
        name: 'Photo',
        params: {
          album_id: album.id,
          photo_index: index
        }
      });
      setTimeout(this.scrollToThumbnail, 800);
    },
    preload (index) {
      let preloadByIndex = (index, offset) => {
        index += offset;
        if (index < 0) index = this.photos.length - index;
        if (index >= this.photos.length) index = index - this.photos.length;
        let img = new Image();
        img.src = this.photos[index].large;
      };
      for (let i = 1; i <= PRELOAD_COUNT; ++i) {
        preloadByIndex(index, +i);
        preloadByIndex(index, -i);
      }
    },
    scrollToThumbnail () {
      let e = id => document.getElementById(id);
      this.$scrollTo(e(this.getThumbnailId(this.index)), 300, {
        container: e('sidebar'),
        offset: -200
      });
    },
    home () {
      this.$router.push({
        name: 'HomeAlbum',
        params: {
          album_id: this.albumId
        }
      });
    },
    next () {
      let nextI = this.index + 1;
      if (nextI >= this.photos.length) nextI = 0;
      this.switchTo(nextI, +1);
    },
    previous () {
      let nextI = this.index - 1;
      if (nextI < 0) nextI = this.photos.length - 1;
      this.switchTo(nextI, -1);
    },
    setPhoto (index) {
      if (index === this.index) return;
      this.switchTo(index, index - this.index);
    },
    switchTo (index, direction) {
      if (this.animationState !== 0) return;
      this.direction = direction;
      this.animationState = -1;
      this.preload(index);
      setTimeout(() => {
        this.index = index;
        this.$router.push({
          name: 'Photo',
          params: {
            album_id: this.albumId,
            photo_index: index
          }
        });
        this.scrollToThumbnail();
        this.animationState = +1;
        setTimeout(() => {
          this.animationState = 0;
        }, 300);
      }, 300);
    },
    thumbnailClass (index) {
      return (this.index === index ? 'thumbnail-selected ' : ' ') +
        'thumbnail-photo';
    }
  },
  computed: {
    photoClass () {
      if (this.animationState === 0) return '';
      if (this.animationState > 0) {
        return this.direction > 0 ? 'animation_in_left' : 'animation_in_right';
      } else {
        return this.direction > 0 ? 'animation_out_right' : 'animation_out_left';
      }
    },
    photoStyle () {
      return {
        backgroundImage: `url('${this.photos[this.index].large}')`
      };
    }
  }
};
</script>

<style scoped lang='scss'>
.button {
  position: fixed;
  text-align: center;
  padding: 10px;
  background: black;
  cursor: pointer;
  color: white;
  opacity: 0.2;
  left: 20px;
  top: 20px;
  z-index: 1000;
  transition: opacity 0.1s ease-out;
}
.button:hover {
  opacity: 0.5;
  transition: opacity 0.2s ease-in;
}
#photo {
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  flex-grow: 1.0;
}
#sidebar {
  width: 300px;
  padding: 10px;
  background-color: white;
  box-shadow: -2px 0px 2px rgba(0, 0, 0, 0.404);
  overflow-y: scroll;
  @include medium_screen {
    display: none;
  }
}
#thumbnails {
  display: grid;
  grid-template-rows: 150px;
  grid-template-columns: 50% 50%;
}
.thumbnail-photo:hover, .thumbnail-selected {
  filter: grayscale(0) !important;
  transition: filter 0.3s ease-in;
}
.thumbnail-photo {
  cursor: pointer;
  object-fit: cover;
  object-position: center;
  filter: grayscale(1);
  transition: filter 0.2s ease-out;
  width: 140px;
  height: 140px;
  box-shadow: 2px 2px 1px rgba(0, 0, 0, 0.384);
}
#gallery {
  perspective: 800px;
  display: flex;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 1.0);
}
#background{
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
#background {
  background: white;
  opacity: 0.7;
}

#photo {
  display: flex;
  justify-content: space-between;
  background-position-x: center;
}

.arrow:hover i {
  color: black;
  transition: color 0.4s ease-out;
}
.arrow i {
  font-size: 64pt;
  color: rgba(0, 0, 0, 0.534);
  transition: color 0.3s ease-in;
}
.arrow {
  display: flex;
  align-items: center;
  height: 100%;
  width: 30%;
  cursor: pointer;
}
#arrow-right {
  justify-content: flex-end;
}

// .animation_out {
//   filter: blur(7px);
//   transition: filter 0.3s ease-in;
// }
// .animation_in {
//   filter: blur(0px);
//   transition: filter 0.3s ease-out;
// }

.animation_out_left {
  animation: in_left 0.3s ease-in;
  animation-direction: alternate-reverse;
}
.animation_in_left {
  animation: in_left 0.3s ease-out;
}
.animation_out_right {
  animation: in_right 0.3s ease-in;
  animation-direction: alternate-reverse;
}
.animation_in_right {
  animation: in_right 0.3s ease-out;
}
@keyframes in_right {
  0% {
    background-position-x: -100vw;
    opacity: 0.6;
  }
  100% {
    opacity: 1.0;
    background-position-x: center;
  }
}
@keyframes in_left {
  0% {
    background-position-x: 100vw;
    opacity: 0.6;
  }
  100% {
    background-position-x: center;
    opacity: 1.0;
  }
}
</style>



