<template>
  <section class='photos-block'>
    <div id="buttons">
      <a v-bind:key="_album.url" v-for="_album in albums"
         v-on:click="setAlbum(_album)" v-bind:href='"#/album/" + _album.id'
         v-bind:class="_album.title == album.title ? 'selected' : ''">
        {{ _album.title }}
      </a>
    </div>
    <div class="photos">
      <img v-bind:src='photo.medium' v-for='(photo, index) in photos'
           v-bind:key='photo.medium' class='photo' v-on:click='showPhoto(index)'>
    </div>
    <infinite-loading @infinite='loadMore' ref='load_more'>
      <span slot='no-more'>
        <a href='#' v-on:click.prevent="scrollToTop()">В начало</a>
      </span>
      <span slot='no-results'>
        <a href='#' v-on:click.prevent="scrollToTop()">В начало</a>
      </span>
    </infinite-loading>
  </section>
</template>

<script>
import InfiniteLoading from 'vue-infinite-loading';

const PRELOADED_PHOTOS_COUNT = 12;

export default {
  name: 'Photos',
  components: { InfiniteLoading },
  data () {
    return {
      albums: window.ALBUMS,
      album: {},
      page: 1
    };
  },
  created () {
    this.album = this.albums[1];
  },
  computed: {
    photos () {
      return this.album.photos.slice(0, PRELOADED_PHOTOS_COUNT * this.page);
    }
  },
  methods: {
    scrollToTop () {
      this.$scrollTo(document.getElementById('buttons'));
    },
    showPhoto (index) {
      this.$emit('photoClicked', this.album, index);
    },
    setAlbum (album) {
      this.page = 1;
      this.album = album;
      this.$refs.load_more.$emit('$InfiniteLoading:reset');
    },
    loadMore ($state) {
      let count = PRELOADED_PHOTOS_COUNT * this.page;
      if (this.album.photos.length <= count) {
        $state.complete();
      } else {
        let left = PRELOADED_PHOTOS_COUNT * this.page;
        let right = Math.min(
          PRELOADED_PHOTOS_COUNT * (this.page + 1),
          this.album.photos.length - 1);
        let counter = right - left;
        for (let i = left; i <= right; ++i) {
          let img = new Image();
          img.src = this.album.photos[i].medium;
          img.onload = () => {
            counter -= 1;
          };
        }
        let timeoutFunc = () => {
          if (counter <= 0) {
            this.page += 1;
            $state.loaded();
          } else {
            console.log(counter);
            setTimeout(timeoutFunc, 300);
          }
        };
        timeoutFunc();
      }
    }
  }
};
</script>

<style scoped lang='scss'>
// @import '../styles/_mixins.scss';
// @import '../styles/_colors.scss';

.selected {
  text-decoration: none !important;
}

#buttons {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  @include large_screen {
    font-size: 24pt;
  };
  @include small_screen {
    font-size: 18pt;
  };
  margin-bottom: 40px;
  a {
    margin: 0 10px;
  }
}
.photos-block {
  width: 100%;
  padding: 50px 0;
}
.photos {
  display: grid;
  @include large_screen {
    grid-template-columns: 20% 20% 20% 20%;
    .photo {
      width: 20vw;
      height: 20vw;
    }
  };
  @include medium_screen {
    grid-template-columns: 45% 45%;
    .photo {
      width: 45vw;
      height: 45vw;
    }
  };
  @include small_screen {
    grid-template-columns: 90%;
    .photo {
      width: 90vw;
      height: 90vw;
    }
  }
  justify-content: center;
  align-items: baseline;
}
.photo {
  cursor: pointer;
  box-shadow: 1px 2px 2px $shadowDark;
  object-fit: cover;
  object-position: center;
}
.photo:hover {
  filter: brightness(0.75);
  transition: filter 0.1s linear;
  box-shadow: 0px 0px 5px $shadowDark;
}
</style>



