<template>
  <header class='header'>
    <div id='working-hours'>
      <span>Время работы:</span>
      <i class='material-icons'>alarm_on</i>
      <span>9:00 - 19:00</span>

      <span>Приём заказов:</span>
      <i class='material-icons'>phone</i>
      <span>9:00 - 21:00</span>
    </div>
    <logo-component></logo-component>
    <div id='contacts'>
      <i class='material-icons'>location_on</i>
      <span id="address">г. Россошь, ул. Линейная, д. 11
        <img id="confectionery" src='../assets/confectionery.jpg'>
      </span>

      <i class='material-icons'>contact_phone</i>
      <span id='phone'>+7(920)460-45-47
        <social-component is-small='true' id='social-buttons'></social-component>
      </span>

      <i class='material-icons'>perm_identity</i>
      <span>Наталья Бондарева</span>
    </div>
  </header>
</template>

<script>
import LogoComponent from './logo.vue';
import SocialComponent from './social.vue';
export default {
  name: 'Header',
  components: { LogoComponent, SocialComponent },
  data () {
    return { };
  }
};
</script>

<style scoped lang='scss'>
  #address, #phone {
    cursor: pointer
  }
  #address:active #confectionery, #phone:hover #social-buttons {
    opacity: 1.0;
    margin-top: 20px;
    transition: opacity 0.4s ease-out;
  }
  #phone:hover #social-buttons {
    margin-top: 0px !important;
  }
  #social-buttons {
    width: 250px;
    margin-left: -80px;
  }
  #confectionery {
    width: 400px;
    right: 5%;
  }
  #confectionery, #social-buttons {
    cursor: default;
    opacity: 0.0;
    margin-top: -100%;
    transition: opacity 0.05s ease-in,
                margin-top 1ms linear 0.05s;
    position: absolute;
    max-width: 90%;
    padding: 20px;
    border-radius: 30px;
    background: rgba(0, 0,0, 0.4);
    box-sizing: border-box;
  }
  .header {
    color: $fontColorHeader;
    height: 200px;
    width: 100%;
    display: flex;
    align-items: center;
    flex-wrap: wrap-reverse;
    justify-content: space-around;
    font-size: 12pt;
  }
  #working-hours {
    display: grid;
    grid-template-columns: auto 40px auto;
    text-align: center;
  }
  #contacts {
    display: grid;
    grid-template-columns: 30px auto;
  }
</style>



