<template>
  <div class="social">
    <a v-if="!isSmall" class="social-button" v-bind:href="viberLink">
      <img src="../assets/social/viber.png">
    </a>
    <a v-if="!isSmall" class="social-button" v-bind:href="whatsappLink">
      <img src="../assets/social/whatsapp.png">
    </a>
    <a v-if="!isSmall" class="social-button" v-bind:href="vkontakteLink">
      <img src="../assets/social/vkontakte.png">
    </a>
    <a v-if="isSmall" class="social-button" v-bind:href="viberLink">
      <img src="../assets/social/viber-small.png">
    </a>
    <a v-if="isSmall" class="social-button" v-bind:href="whatsappLink">
      <img src="../assets/social/whatsapp-small.png">
    </a>
    <a v-if="isSmall" class="social-button" v-bind:href="vkontakteLink">
      <img src="../assets/social/vkontakte-small.png">
    </a>
  </div>
</template>

<script>
const PHONE_NUMBER = '79204604547';

export default {
  name: 'WhyWe',
  data () {
    return {
      isSmallMode: false,
      viberLink: 'viber://add?number=' + PHONE_NUMBER,
      whatsappLink: 'whatsapp://send?phone=' + PHONE_NUMBER,
      vkontakteLink: 'https://vk.com/sweetrossosh'
    };
  },
  props: ['isSmall'],
  created () {
    if (this.isSmall) {
      this.isSmallMode = true;
    }
  }
};
</script>

<style scoped lang="scss">
.social {
  width: 100%;
  margin: 20px 0;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.social-button {
  height: 40px;
  margin: 10px;
}
.social-button img:hover {
  filter: brightness(0.8);
  transition: filter 0.4s ease-out;
}
.social-button img {
  height: 40px;
  transition: filter 0.2s ease-out;
}
</style>



